

.title{
  font-family: $stack-lora;
  margin-top: 25px;
  font-size: 40px;
  text-transform: capitalize;
  text-align: left;
 @include phone{
   margin-top: 75px;
 }
}

.block-title{
  text-align: left;
  color: white;
  background: black;
  text-transform: uppercase;
  font-size: 18px;
  padding: 12px 7px;
  letter-spacing: 0.12em;

  &::before{
    content: "//";
    padding: 7px;
  }

}

